<template>
  <Modal
    :isActive="isActive"
    title=""
    @close="close"
  >
    <p>
      <br/>
      {{Math.floor(duration / 60)}}
    </p>
    <div slot="footer">
      <button class="button is-success" @click="myEnter"></button>
      <button class="button" @click="close"></button>
   </div>
   </Modal>
</template>

<script>
import { mapActions, mapState } from 'vuex';
import Modal from '@/components/common/Modal';

export default {
  props: [
    'isActive',
  ],
  computed: {
    ...mapState('koneko/contests', [
      'duration',
      'id',
    ]),
  },
  methods: {
    ...mapActions('koneko/contests', [
      'enter',
      'getContest',
    ]),
    async myEnter() {
      await this.enter();
      this.$emit('close');
    },
    close() {
      this.$emit('close');
    },
  },
  components: {
    Modal,
  },
};
</script>
